.auth {
  padding: 10px;
  margin-top: 150px;
  display: flex;
  align-items: center;
  justify-content: center;
  flex-direction: column;
  & > form {
    flex: 0 1 300px;
  }
  &__registr {
    margin-left: 10px;
    display: inline-flex;
  }
  &__link {
    color: rgb(235, 97, 97);
    &:hover {
      color: rgb(0, 0, 0);
    }
  }
}
